@import url(https://fonts.googleapis.com/css?family=PT+Sans:400,700|PT+Serif:700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body, .App {
  margin: 0;
  width: 100%;
  height: 100%;
  padding: 0;
  box-sizing: border-box;
  color: #1F1F1F;
  background: #000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  font-size: 16px;
  font-family: "PT Sans", "roboto", "Source Sans Pro", "sans-serif" !important; }

div,
a,
img {
  box-sizing: border-box;
  text-decoration: none; }

.screen {
  width: 375px;
  height: 812px;
  background: #fff;
  margin: 0 auto;
  overflow: hidden;
  position: relative; }

.mainmenu {
  background: rgba(252, 223, 105, 0.3); }

.footbar {
  height: 64px;
  width: 100%;
  padding: 0 24px;
  background: #fff;
  box-shadow: 0 0 10px #fcdf69;
  position: absolute;
  bottom: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between; }
  .footbar img {
    height: 28px;
    fill: #FF7B4A;
    cursor: pointer; }

.mainmenu {
  height: 100%; }

.topbar {
  height: 74px;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 0 16px;
  -webkit-align-items: center;
          align-items: center; }
  .topbar .name {
    font-size: 18px;
    font-weight: bold;
    color: #3C4D64; }
  .topbar .addbtn {
    width: 38px;
    height: 38px;
    background: #FF7B4A;
    border-radius: 19px;
    text-align: center;
    vertical-align: center;
    font-size: 50px;
    color: #fff;
    line-height: 0.8; }

.feedCard {
  background: #fff;
  width: 95%;
  margin: 8px auto 0;
  border-radius: 6px;
  padding: 16px 16px 20px;
  box-shadow: 0 0 8px #fcdf69;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center; }
  .feedCard .profilePic {
    width: 60px;
    height: 60px;
    border-radius: 30px;
    background: #ccc;
    margin-right: 24px; }
  .feedCard .Messages {
    line-height: 1.7;
    width: calc(100% - 84px); }
    .feedCard .Messages .sent {
      font-size: 12px;
      color: rgba(0, 0, 0, 0.5); }
    .feedCard .Messages .message {
      margin-top: 4px;
      width: 100%;
      font-size: 18px; }
    .feedCard .Messages .recipe {
      display: -webkit-flex;
      display: flex;
      -webkit-align-items: center;
              align-items: center;
      color: #3C4D64;
      font-weight: bold;
      font-size: 14px; }
      .feedCard .Messages .recipe .fork {
        width: 40px;
        margin-right: 8px;
        display: inline-block; }
    .feedCard .Messages .shared_recipe {
      width: 100%;
      display: -webkit-flex;
      display: flex;
      -webkit-align-items: center;
              align-items: center;
      margin: 8px 0;
      border-radius: 6px;
      overflow: hidden;
      border: 1px solid #ccc; }
      .feedCard .Messages .shared_recipe .preview {
        width: 46px;
        height: 46px;
        background: #ccc;
        margin-right: 16px; }
      .feedCard .Messages .shared_recipe .title {
        font-weight: bold;
        font-size: 14px; }
      .feedCard .Messages .shared_recipe .recipe_content {
        font-size: 12px; }

